@import '../../../assets/scss/color_variables';
@import '../../../assets/scss/screen_sizes';
.SideDrawer{
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: #fff;
    padding: 32px 16px;
    transition: transform 0.3s ease-out;
    .SideDrawerNavigation{
        display: flex;
        flex-direction: column;
        a{
            color: $red;
            text-decoration: none
        }
    }
    .Categories{
        border-bottom: 1px solid #ccc;
    }
}
.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);
}
@media (min-width: $large_min_width){
    .SideDrawer{
        display: none;
    }
}
